import React, { useEffect, useState } from 'react';
import './_task.scss';
import TaskModal from 'components/Modal/TaskModal';
import Subtask from './Subtask';
import taskService from '../../../services/taskService';

function Task ({ task, onDelete }) {

  const [expandedTask, setExpandedTask] = useState(false);
  const [subtasks, setSubtasks] = useState(task.subtasks || []);
  const [modalOpen, setModalOpen] = useState(false);

  async function toggleCompleted (task) {
    task.checked = !task.checked;
    await taskService.updateTask(task);
  }

  return (
    <li className={ (task.checked ? 'done' : '') + ' parent-hover task' } title={ task.timestamp }>
      <div>
        <label className="left">
          <input
            type="checkbox"
            checked={ task.checked }
            onChange={ () => toggleCompleted(task) }
          />
          <span> </span>
        </label>
        <a
          href="#!" className={ 'task-name ' + (task.checked ? '' : '') } onClick={ () => setModalOpen(true) }
        >{ task.name }</a>

        <span className="right">
          <button
            className="child-hover btn-subtle ml-5 material-icons right"
            onClick={ () => onDelete(task) }
          >delete
          </button>
          <button
            className="child-hover toggle-expand btn-subtle ml-5 material-icons right"
            onClick={ () => setExpandedTask(!expandedTask) }
          >add
          </button>
          <TaskModal
            trigger={ {
              className: 'child-hover btn-subtle ml-5 material-icons right',
              text: 'edit'
            } } task={ { ...task } }
            modalOpen={ modalOpen }
            setModalOpen={ setModalOpen }
          />
        </span>
      </div>
      {
        expandedTask &&
        <React.Fragment>
          <blockquote className="task-more ml-50">{ task.description }</blockquote>
          <Subtask extraClass={ 'ml-50' } setSubtasks={ setSubtasks } subtasks={ subtasks } task={ task }/>
        </React.Fragment>
      }
    </li>
  );
}

export default Task;
